import React, { Component } from 'react';
import { navigate } from 'gatsby';

import { login } from '../../services/auth';
import { PATH_LANDING } from '../../constants/routes';

import { ComponentProps } from '../ComponentProps.d';

import SignInFormView from './view-md/sign-in-form';

const INITIAL_STATE = {
  email: '',
  password: '',
  error: '',
  inProgress: false,
};

interface SignInFormProps extends ComponentProps {
}

type SignInFormStates = typeof INITIAL_STATE;

class SignInForm extends Component<SignInFormProps, SignInFormStates> {

  state = INITIAL_STATE;

  onSubmit = (event: React.FormEvent): void => {
    const { email, password } = this.state;
    this.setState({inProgress: true});

    login({email, password})
      .then((res) => {
        if (!res || res.kind !== 'ok') {
          this.setState({...INITIAL_STATE, error: res ? res.kind : 'unknown-error'});
        }
        else {
          this.setState({ ...INITIAL_STATE });
          navigate(PATH_LANDING, { replace: true });
        }
      })
      .catch(error => {
        this.setState({ error: error.message || error.toString() });
      })
      .finally(() => this.setState({inProgress: false}));

    event.preventDefault();
  };

  onChange = (event: React.ChangeEvent<HTMLInputElement>): void => {
    // !!! The variable, 'name' shold be 'email' or 'password'
    //@ts-ignore
    const { name, value } = event.target;
    //@ts-ignore
    this.setState({ [name]: value });
  };

  isValidFormData(): boolean {
    const { email, password } = this.state;
    // simple check
    return !!(password && email)
  }

  render(): React.ReactElement {
    const isSubmitButtonDisabled = this.state.inProgress || !this.isValidFormData();
    const { email, password, error, inProgress } = this.state;

    // TODO: fix type error
    // @ts-ignore
    return ( <SignInFormView
      onSubmit={this.onSubmit}
      email={email}
      onChangeEmail={this.onChange}
      password={password}
      onChangePassword={this.onChange}
      isSubmitButtonDisabled={isSubmitButtonDisabled}
      errorMessage={error}
      inProgress={inProgress}
      {...this.props}
      />
    );
  }
}

export { SignInForm };
