import React from 'react';
import {
  CircularProgress,
  Typography,
  Button,
  TextField,
  Fade,
} from "@material-ui/core";

// styles
import useStyles from "./styles";

/**
 * Sign-up form view
 * 
 * @param {function} t i18n function
 */
const SignUpForm = ({
  t,
  onSubmit,
  username,
  onChangeUsername,
  email,
  onChangeEmail,
  password,
  onChangePassword,
  passwordConfirmation,
  onChangePasswordConfirmation,
  isSubmitButtonDisabled,
  errorMessage,
  inProgress,
}) => {
  const classes = useStyles();

  return (
  <>
    <TextField
      name="username"
      InputProps={{
        classes: {
          underline: classes.textFieldUnderline,
          input: classes.textField,
        },
      }}
      value={username}
      onChange={onChangeUsername}
      margin="normal"
      placeholder="Full Name"
      type="text"
      fullWidth
    />
    <TextField
      name="email"
      InputProps={{
        classes: {
          underline: classes.textFieldUnderline,
          input: classes.textField,
        },
      }}
      value={email}
      onChange={onChangeEmail}
      margin="normal"
      placeholder="Email Adress"
      type="email"
      fullWidth
    />
    <TextField
      name="password"
      InputProps={{
        classes: {
          underline: classes.textFieldUnderline,
          input: classes.textField,
        },
      }}
      value={password}
      onChange={onChangePassword}
      margin="normal"
      placeholder="Password"
      type="password"
      fullWidth
    />

    <Fade in={!!errorMessage}>
      <Typography color="secondary" className={classes.errorMessage}>
        Something is wrong with your sign-up information :(
      </Typography>
    </Fade>

    <div className={classes.creatingButtonContainer}>
      {inProgress ? (
        <CircularProgress size={26} className={classes.circularProgress}/>
      ) : (
        <Button
          onClick={onSubmit}
          disabled={isSubmitButtonDisabled}
          size="large"
          variant="contained"
          color="primary"
          fullWidth
          className={classes.createAccountButton}
        >
          Create your account
        </Button>
      )}
    </div>

  </>  
  );
}

export default SignUpForm;
