import React, { useState } from "react";
import { withTranslation } from "react-i18next"
import {
  Grid,
  Typography,
  Button,
  Tabs,
  Tab,
} from "@material-ui/core";
import { Link } from 'gatsby';
import classnames from "classnames";

// components
import { SignInForm } from "../../components/SignIn";
import { SignUpForm } from "../../components/SignUp";

import { PATH_PASSWORD_FORGET } from "../../constants/routes";

import { getSearchParam } from "../../utils/get-location";

// styles
import useStyles from "./styles";

// logo
import logo from "./logo.svg";
import google from "../../../static/img/google.svg";

const I18N_ID = 'Others';

function Login({
  t,
  location,
}) {
  const classes = useStyles();
  // 0: login, 1: sign-up
  const params = getSearchParam(location);
  const idx = (params && params.length > 0) ? parseInt(params[0]) : 0;
  // local
  const [activeTabId, setActiveTabId] = useState(idx);
  //console.log('>>>>>>>>>>>', activeTabId);

  return (
    <Grid container className={classes.container}>
      <div className={classes.logotypeContainer}>
        <img src={logo} alt="logo" className={classes.logotypeImage} />
        <Typography className={classes.logotypeText}>CARBON</Typography>
      </div>
      <div className={classes.formContainer}>
        <div className={classes.form}>
          <Tabs
            value={activeTabId}
            onChange={(e, id) => setActiveTabId(id)}
            indicatorColor="primary"
            textColor="primary"
            centered
          >
            <Tab label="Login" classes={{ root: classes.tab }} />
            <Tab label="New User" classes={{ root: classes.tab }} />
          </Tabs>
          {activeTabId === 0 && (
            <React.Fragment>
              <Typography variant="h2" className={classes.greeting}>
                Hey, User
              </Typography>

              <SignInForm />

              <div className={classes.formDividerContainer}>
                <div className={classes.formDivider} />
                <Typography className={classes.formDividerWord}>or</Typography>
                <div className={classes.formDivider} />
              </div>

              <Button disabled size="large" className={classes.googleButton}>
                <img src={google} alt="google" className={classes.googleIcon} />
                &nbsp;Sign in with Google
              </Button>
              
              <div className={classes.formButtons}>
                <Button
                  color="primary"
                  className={classes.forgetButton}
                  component={Link}
                  to={PATH_PASSWORD_FORGET}
                >
                  Forget Password
                </Button>

                <Button
                  color="primary"
                  className={classes.forgetButton}
                  onClick={() => setActiveTabId(1)}
                >
                  New Account
                </Button>
              </div>
            </React.Fragment>
          )}
          {activeTabId === 1 && (
            <React.Fragment>
              <Typography variant="h2" className={classes.greeting}>
                Welcome!
              </Typography>
              <Typography variant="h4" className={classes.subGreeting}>
                Create your account
              </Typography>

              <SignUpForm />

              <div className={classes.formDividerContainer}>
                <div className={classes.formDivider} />
                <Typography className={classes.formDividerWord}>or</Typography>
                <div className={classes.formDivider} />
              </div>

              <Button disabled size="large" className={classnames(classes.googleButton)}>
                <img src={google} alt="google" className={classes.googleIcon}/>
                &nbsp;Sign in with Google
              </Button>
            </React.Fragment>
          )}
        </div>
        <Typography color="primary" className={classes.copyright}>
          © 2014-2019 CARBON, All rights reserved.
        </Typography>
      </div>
    </Grid>
  );
}

export default withTranslation(I18N_ID)(Login);
