
/**
 * 현재 Context의 location object를 반환한다.
 * 
 * @param {*} ctx 
 */
export function getLocation({ ctx, location }: {ctx?: any; location?: any}) {
  if (location) return location; // 
  // TODO:
  // SSR env
  // window.location: https://www.tutorialrepublic.com/javascript-tutorial/javascript-window-location.php
  // koa request: https://github.com/koajs/koa/blob/master/docs/api/request.md
  if (ctx && ctx.request) return {
    'href': ctx.request.href,
    'search': ctx.request.search,
    'protocol': '',
    'host': '',
    'hostname': '',
    'port': '',
    'pathname': '',
    'hash': ''
  };

  // browser env
  if (typeof window !== 'undefined') return window.location;

  return null;
}

export function getSearchParam({ search }: {search?: string}, key?: string) {
  if (!search || search[0] !== '?') return [];
  const params = search.substring(1).split("&");
  if (!key) return params;
  return params.find((ele) => ele === key);
}
